export default function Home() {
  return `
        <div class='main-content'>
        <h1 class='main-content__welcome'>Welcome to my message board</h1>
        <h3 class='main-content__text'>Using Firebase</h3>
        <div>

        <section class='main-content__signup'>
            <h4>Sign up</h4>
            <form>
                <input type="email" placeholder="email" id='signup-email' required />
                <input type="password" placeholder="password" id='signup-password' required />
              <button id='signup-submit'>Sign up</button>
            </form>
        </section>
      
        <section class='main-content__login'>
            <h4>Log in</h4>
            <form>
                <input type="email" placeholder="email" id='login-email' required />
                <input type="password" placeholder="password" id='login-password' required />
              <button id='login-submit'>Sign up</button>
            </form>
        </section>
        `;
}
